}

body:has(.modal-container.active) {
  overflow-y: hidden;
}

.modal-container {
  z-index: 9999;
  background: var(--modal-bg-color);
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
}

.modal-container > .modal {
  padding: 24px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-container > .alert {
  max-width: 100%;
  width: 490px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 12px;
  padding: 20px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
