$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);

:root {
  --primary: #16abf8;
  --alternative: #E5E5E5;
  --modal-bg-color: #00000080;
}

@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);


body {
    margin: 0;
    font-family: "Poppins",sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f4f4f4;
}

h1 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
}

button {
  font-family: "Poppins",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: pointer;
}

button.btn {
  border-radius: 45px;
  border: 0;
  padding: 13.5px 29px;
  font-size: 18px;
  font-weight: 600;    
  line-height: 27px;
}

.container {
  max-width: 1000px;

  &.center {
    margin-left: auto;
    margin-right: auto;
  }
}

.btn.disabled, .btn:disabled, fieldset:disabled .btn {
  pointer-events: none;
  opacity: .65;
}

body:has(.modal-container.active) {
  overflow-y: hidden;
}

.modal-container {
  display: none;
  position: fixed;
  top: 0;
  z-index: 9999;
  background: var(--modal-bg-color);
  width: 100vw;
  height: 100vh;

  & > .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 24px 30px;
  }

  & > .alert {
    max-width: 100%;
    width: 490px;
    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 12px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    padding: 20px 30px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }

  & > .alert > [class^=icon-] {
    margin-right: 13px;
  }

  & > .alert > h3 {
    display: inline-block;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    margin: 0;
    color: #111111;
  }

  /* modal-delete */

  & > .modal-delete {
    width: 490px;
    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

    padding: 40px 62px 43px 62px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;

    & > .modal-delete-icon {
      text-align: center;
      height: 84px;
      margin-bottom: 34px;
    }

    & > .modal-delete-icon::before {
      content: '';
      width: 84px;
      height: 84px;
      background-image: url('/src/svg/icon-alert.svg');
      display: inline-block;
    }

    & > .modal-delete-title {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      text-align: center;
      color: #111111;
      margin: 0;
    }

    & > .modal-delete-title > span {
      font-weight: 700;
    }

    & > .modal-delete-action-container {
      text-align: center;
      margin-top: 46px;

      & > *:first-child {
        margin-right: 20px;
      }
    }

    & .modal-delete-cancel-button {
      font-weight: 600;
      font-size: 18px;
      line-height: 27px;
      color: #4A4A4A!important;
      width: 150px;
      box-sizing: border-box;
      -webkit-box-sizing: border-box;
    }

    & .modal-delete-confirm-button {
      width: 150px;
      font-weight: 600;
      font-size: 18px;
      line-height: 27px;
      color: #FFFFFF!important;
      box-sizing: border-box;
      -webkit-box-sizing: border-box;
    }
  }
}

.modal-container.active {
  display: block;
}

.btn-primary {
  color: #fff;
  background-color: var(--primary);
  border-color: var(--primary);
}

.btn-alternative {
  background-color: unset;
  border: 1px solid var(--alternative)!important;
}

.btn-circle {
  border-radius: 50%;
  padding: 0!important;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 54px;
  aspect-ratio: 1;
}

span[class^="icon-"] {
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  vertical-align: middle;
}

.wh-18 {
  width: 18px!important;
  height: 18px!important;
}

.mr-15 {
  margin-right: 15px;
}

.btn.btn-light {
  background-color: unset;
}

.btn.btn-simple {
  border-radius: unset;
  border: 0;
  padding: unset;   
  line-height: 27px;
}

.btn:has(span[class^="icon-"]) {
  display: flex;
  gap: 6px;
}

.todo-header {
  display: flex;
  flex-direction: row;
  // justify-content: space-between;
  align-items: center;
  margin: 43px 0 55px
}

.todo-title{
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 0;
}

.activity-title-edit {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 54px;
  color: #111111;
  border: 0;
  background: 0;
  appearance: none;
  outline: none;
  // display: none;

  &:focus-visible, &:focus {
    display: block!important;
    border-bottom: 1px solid;
    margin-bottom: -1px;
  }
}

.dashboard-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 26px 20px;
}

.activity-content {
  display: grid;
  grid-gap: 10px;
}

.ml-auto {
  margin-left: auto;
}

.todo-back-button {
  margin-right: 19px;
}

.todo-title-edit-button {
  margin-left: 23px;
}

.activity-sort-button {
  margin-right: 18px;
}

.todo-empty-state, .activity-empty-state {
  width: 100%;
  display: flex;
  justify-content: center;
  grid-column: 1/5;
}

.input-group {
  padding: 9px 0;
  
  margin-top: 8px;

  & .input-group-label {
    margin-bottom: 9px;
    display: block;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    text-transform: uppercase;
  }

  & .input-group-input {
    background: #FFFFFF;
    border: 1px solid var(--alternative);
    border-radius: 6px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    padding: 14px 18px;

    &:active, &:focus, &:focus-visible {
      outline: none !important;
      border: 1px solid var(--primary)!important;
    }
  }
}

.option-container {
  margin: 0;
  padding: 0;

  & > li {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #4A4A4A;
    list-style: none;
    background: #FFFFFF;
    border: 1px solid #E5E5E5;
    padding: 14px 17px;
    cursor: pointer;
  }

  &.rounding-first-child > li:first-child {
    border-radius: 6px 6px 0px 0px;
  }

  & > li:last-child {
    border-radius: 0px 0px 6px 6px;
  }

  & .priority-indicator {
    margin-right: 19px;
  }

  & > li.selected::after {
      content: '';
      background-image: url(./svg/icon_checked.svg);
      background-size: contain;
      width: 18px;
      height: 18px;
      display: inline;
      vertical-align: middle;
      float: right;
  }
}

.priority-indicator {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  display: inline-block;
}


/* icons */

.icon-plus {
  background-image: url(./svg/icon-plus.svg);
}

.icon-delete {
  background-image: url(./svg/icon-delete.svg);
}

.icon-back {
  background-image: url(./svg/icon-back.svg);
}

.icon-sort {
  background-image: url(./svg/icon-sort.svg);
}

.icon-sort-newest {
  background-image: url(./svg/icon-sort-newest.svg);
}

.icon-sort-oldest {
  background-image: url(./svg/icon-sort-oldest.svg);
}

.icon-sort-az {
  background-image: url(./svg/icon-sort-az.svg);
}

.icon-sort-za {
  background-image: url(./svg/icon-sort-az.svg);
}

.icon-sort-unfinished {
  background-image: url(./svg/icon-sort-unfinished.svg);
}

.icon-edit {
  background-image: url(./svg/icon-edit-h.svg);
}

.icon-close {
  background-image: url(./svg/icon-close.svg);
}

.icon-alert-sm {
  background-image: url(./svg/icon-alert-sm.svg);
}

/* Background Color */

.bg-red {
  background-color: #ED4C5C;
}

.bg-yellow {
  background-color: #FFCE31;
}

.bg-green {
  background-color: #00A790;
}

.bg-cyan {
  background-color: #43C4E3;
}

.bg-purple {
  background-color: #B01AFF;
}

